footer {
  font-size: 1.6rem;
  position: absolute;
  width: 100%;
  bottom: 0;
}

.grid-footer {
  color: #acb3c2;
  margin-bottom: 1rem;
  margin-top: 2rem;
}

.grid-footer a {
  color: #727e96;
}

#score {
  margin-bottom: 6rem;
  background: #5764c6;
  border-radius: 50%;
  color: rgba(255, 255, 255, .85);
  font-weight: 300;
  line-height: 1;
  position: relative;
  vertical-align: middle;
  height: 270px;
  width: 270px;
}

#score-content {
  left: 50%;
  position: absolute;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  z-index: 1;
}

#score-content-value {
  font-size: 15rem;
}

#score-content-percentage {
  font-size: 3rem;
  margin-top: 1rem;
}

#score-content-badge {
  position: absolute;
  top: 5%;
  right: 10%;
  z-index: 100;
  background: #5764c6;
  background-clip: padding-box;
  border-radius: 50%;
  box-shadow: 0 0 0 0.2rem #fff;
  color: #fff;
  display: inline-block;
}

#score-content-badge-content {
  font-size: 3rem;
  width: 50px;
  height: 50px;
  text-align: center;
  white-space: nowrap;
  -webkit-transform: translate(50%, -50%);
  transform: translate(0%, 15%);
}

@media (min-width: 400px) {
  #score {
    height: 350px;
    width: 350px;
  }
  #score-content-value {
    font-size: 22rem;
  }
  #score-content-percentage {
    font-size: 3rem;
    margin-top: 1rem;
  }
  #score-content-badge {
    top: 5%;
    right: 10%;
  }
  #score-content-badge-content {
    font-size: 3.5rem;
    width: 60px;
    height: 60px;
    -webkit-transform: translate(0%, 20%);
            transform: translate(0%, 20%);
  }
}

@media (min-width: 480px) {
  #score {
    height: 400px;
    width: 400px;
  }
  #score-content-value {
    font-size: 24rem;
  }
  #score-content-percentage {
    font-size: 4rem;
    margin-top: 1rem;
  }
  #score-content-badge {
    top: 5%;
    right: 10%;
  }
  #score-content-badge-content {
    font-size: 4rem;
    width: 70px;
    height: 70px;
    -webkit-transform: translate(0%, 20%);
            transform: translate(0%, 20%);
  }
}


