/**
 * Sticky footers!
 */

html {
  height: 100%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}


body {
  min-height: 100%;
  padding-bottom: 20rem;
  position: relative;
}

/**
 * Default
 */

body {
  font-family: Lato, "Open Sans", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 1.8rem;
}


/**
 * helpers
 */

.text-center {
  text-align: center;
}

.text-left {
  text-align: left;
}

.text-right {
  text-align: right;
}

.mt-20 {
  margin-top: 2rem;
}

.mt-30 {
  margin-top: 3rem;
}

.bg-gray {
  background: #f8f9fa;
  color: #667189;
  padding: 1rem;
}

.form-checkbox {
  margin: .7rem 0;
}

/**
 * App specific
 */

.app-title {
  margin-top: 5rem;
  margin-bottom: 5rem;
}

p.info {
  margin-bottom: 3rem;
}

/**
 * Animations
 */

.fade-in {
  animation: fadeIn 500ms;
  -moz-animation: fadeIn 500ms;
  -webkit-animation: fadeIn 500ms;
  -o-animation: fadeIn 500ms;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@-webkit-keyframes fadeIn {
  /* Safari and Chrome */
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

